<template>
  <div class="project">
    <b-loading :is-full-page="false" :active.sync="isLoading" :can-cancel="false"></b-loading>

    <navigation-menu v-if="project && !isLoading" :title="project.name" :arrowBack="arrowBack">
      <div class="deleteButton" v-on:click.stop="confirmCustomDelete()" v-if="!example && arrowBack">
        <i class="fas fa-trash"></i>
      </div>
    </navigation-menu>

    <div class="project-details" v-if="project && !isLoading">
      
      <div class="description">
        <strong>Descrição:</strong> 
        <p>{{ project.description }}</p>
      </div>

      <div class="plans">
        <b-collapse class="plan-item" 
          v-for="plan in project.plans" 
          :key="plan.id"
          :open.sync="plan.selected"
          :class="{ 'active': plan.selected == true}"
        >
          <div
            slot="trigger"
            class="header"
            role="button"
          >
            <p class="title">
              <i class="fas fa-map"></i>
              {{plan.createdAt | moment("DD/MM/YYYY HH:mm")}}</p>
            <a class="icon">
              <i class="fas" :class="plan.selected ? 'fa-angle-down' : 'fa-angle-right'"></i>
            </a>
          </div>
          <div class="content">
            <plan-details :plan="plan"></plan-details>
          </div>
        </b-collapse>
      </div>
      

    </div>
    <div class="errorNotFound" v-if="error && !isLoading">
      Desculpe, este mapa não foi encontrado.<br /> :(
    </div>

    <!-- <div class="add">
      <div v-if="addClicked" class="addExpansion">
        <div class="addItem" @click="addOrthomosaic">
          Adicionar
        </div>
        <div class="addItem" @click="createOrthomosaic">
          Criar
        </div>
      </div>
      
      <div class="addExpand" @click="addClicked = !addClicked" >
        Orthomosaicos
      </div> 
    </div> -->
  </div>
</template>

<script>
  import NavigationMenu from '../NavigationMenu'
  import PlanDetails from '../plan/PlanDetails'

  export default {
    name: 'project-details',
    // props: ['projectID'],
    props: {
      projectId: {
        type: String
      },
      share: {
        type: Object,
        default: null
      },
      example: {
        type: Boolean,
        default: false
      },
    },
    components: {
      NavigationMenu,
      "plan-details": PlanDetails
    },
    data() {
      return {
        project: { },
        addClicked: false,
        error: false,
        isLoading: false
      }
    },
    mounted() {
      this.isLoading = true
      if(this.share && this.share.linkSharing){
        this.arrowBack = false
      }
      this.$store.dispatch('getProject', this.projectId)
      .then( (project) => {

        this.project = project
        
        if(this.project.plans != null){
          this.$store.dispatch('getPlans', 'project='+this.project.id)
            .then( (plans) => {
              plans.forEach((plan,index) => {
                if(index == 0 ){
                  plan.selected = true
                }else{
                  plan.selected = false
                }
              })
              this.project.plans = plans
          });
        }
        else{
          this.project.plans = []
        }
        this.isLoading = false

      })
      .catch(()=>{
        this.isLoading = false
        this.error = true
      })
    },
    methods: {

      centerMap(coords) {
        this.$root.$emit('centerMap', coords)
      },

      deleteItem() {
        if(!this.share){
          this.$store.dispatch('deleteProject', this.projectId)
          .then( () => {
            // this.$parent.removeItem(this.object)
            this.$notify({
              group: 'global',
              type: 'is-success',
              duration: 2000,
              title: 'Projeto removido'
            });
          })
        }
        else{
          this.$store.dispatch('deleteSharing', this.share.id)
          .then( () => {
            this.$notify({
              group: 'global',
              type: 'is-success',
              duration: 2000,
              title: 'Projeto compartilhado removido'
            });
          })
        }        
      },

      confirmCustomDelete() {
        this.$dialog.confirm({
          title: 'Deletando Projeto',
          message: 'Você tem certeza que deseja <b>deletar</b> o projeto ? Isso é uma ação sem volta.',          
          confirmText: 'Deletar Projeto',
          type: 'is-danger',
          hasIcon: true,
          onConfirm: () => this.deleteItem()
        })
      },



    },
    computed: {
    },
  }
</script>

