<template>
  <div class="album">
  
    <b-loading :is-full-page="false" :active.sync="isLoading" :can-cancel="false"></b-loading>

    <navigation-menu v-if="album && !isLoading" :title="album.name" :arrowBack="arrowBack">
      <div class="deleteButton" v-on:click.stop="confirmCustomDelete()" v-if="!example && arrowBack">
        <i class="fas fa-trash"></i>
      </div>
    </navigation-menu>

    <album-details v-if="album && !isLoading" :album="album"></album-details>
    <album-thumbnails v-if="album && !isLoading" :album="album"></album-thumbnails>

    <div class="errorNotFound" v-if="error && !isLoading">
      Desculpe, este mapa não foi encontrado.<br /> :(
    </div>

  </div>

  <!-- <div class="add">
    <div v-if="addClicked" class="addExpansion">
      <div class="addItem" @click="addOrthomosaic">
        Adicionar
      </div>
      <div class="addItem" @click="createOrthomosaic">
        Criar
      </div>
    </div>
    
    <div class="addExpand" @click="addClicked = !addClicked" >
      Orthomosaicos
    </div> 
  </div> -->
</template>

<script>
  import NavigationMenu from '../NavigationMenu'
  import AlbumDetails from './AlbumDetails'
  import AlbumChunkThumbnails from './AlbumThumbnails'

  export default {
    name: 'album',
    // props: ['albumId'],
    components: {
      NavigationMenu,
      "album-details": AlbumDetails,
      "album-thumbnails": AlbumChunkThumbnails
    },
    props: {
      albumId: {
        type: String
      },
      share: {
        type: Object,
        default: null
      },
      example: {
        type: Boolean,
        default: false
      },
    },
    data() {
      return {
        album: null,
        addClicked: false,
        error: false,
        isLoading: false 
      }
    },
    mounted() {
      if(this.share && this.share.linkSharing){
        this.arrowBack = false
      }
      this.isLoading = true
      this.$store.dispatch('getAlbum', this.albumId)
      .then( (album) => {
        this.album = album
        this.isLoading = false
      })
      .catch(()=>{
        this.isLoading = false
        this.error = true
      })
    },
    methods: {

      centerMap(coords) {
        this.$root.$emit('centerMap', coords)
      },

      deleteItem() {
        if(!this.share){
          this.$store.dispatch('deleteAlbum', this.albumId)
          .then( () => {
            this.$notify({
              group: 'global',
              type: 'is-success',
              duration: 2000,
              title: 'Album removido'
            });
          })
        }
        else {
          this.$store.dispatch('deleteSharing', this.share.id)
          .then( () => {
            this.$notify({
              group: 'global',
              type: 'is-success',
              duration: 2000,
              title: 'Album compartilhado removido'
            });
          })
        }        
      },

      confirmCustomDelete() {
        this.$dialog.confirm({
          title: 'Deletando Album',
          message: 'Você tem certeza que deseja <b>deletar</b> o album ? Isso é uma ação sem volta.',          
          confirmText: 'Deletar Album',
          type: 'is-danger',
          hasIcon: true,
          onConfirm: () => this.deleteItem()
        })
      },



    },
    computed: {
    },
  }
</script>

