import axios from 'axios'

export default {
  state: {
    status: '',
    token: localStorage.getItem('token') || '',
    user: {}

  },

  getters: {
    isLoggedIn: state => !!state.token,
    authStatus: state => state.status,
    getUser: state => state.user
  },

  mutations: {
    auth_request(state) {
      state.status = 'loading'
    },
    auth_success(state, payload) {
      state.status = 'success'
      state.token = payload.token
      state.user = payload.user
    },
    auth_error(state) {
      state.status = 'error'
    },
    logout(state) {
      state.status = ''
      state.token = ''
    },
  },

  actions: {
    login({
      commit
    }, user) {
      return new Promise((resolve, reject) => {
        commit('auth_request')
        axios({
            url: '/api/v2/login',
            data: user,
            method: 'POST'
          })
          .then(resp => {
            const token = resp.data.token
            const user = resp.data.user
            localStorage.setItem('token', token)
            axios.defaults.headers.common['Authorization'] = token
            commit('auth_success', {
              token,
              user
            })
            resolve(resp)
          })
          .catch(err => {
            commit('auth_error')
            localStorage.removeItem('token')
            reject(err)
          })
      })
    },
    facebookLogin({
      commit,
      state
    }, facebookUser) {
      return new Promise((resolve, reject) => {
      // console.log("-----", facebookUser)
        axios.post('/api/v2/facebooklogin', {
            id: facebookUser.id,
            name: facebookUser.name,
            email: facebookUser.email
          })
          .then(resp => {
            const token = resp.data.token
            const user = resp.data.user
            localStorage.setItem('token', token)
            axios.defaults.headers.common['Authorization'] = token
            commit('auth_success', {
              token,
              user
            })
            resolve(resp)
          })
          .catch(e => {
            reject(e)
          });
      })
    },
    register({
      commit
    }, user) {
      return new Promise((resolve, reject) => {
        commit('auth_request')
        axios({
            url: '/api/v2/user',
            data: user,
            method: 'POST'
          })
          .then(resp => {
            if (resp.data.message == "User already registered") {
              commit('auth_error', resp)
              localStorage.removeItem('token')
              reject(resp)
            } else if (resp.data.message == "User registered") {
              const token = resp.data.token
              const user = resp.data.user
              localStorage.setItem('token', token)
              axios.defaults.headers.common['Authorization'] = token
              commit('auth_success', {
                token,
                user
              })
              resolve(resp)
            }
          })
          .catch(err => {
            commit('auth_error', err)
            localStorage.removeItem('token')
            reject(err)
          })
      })
    },

    logout({
      commit
    }) {
      return new Promise((resolve, reject) => {
        commit('logout')
        axios({
            url: '/api/v2/logout',
            method: 'POST'
          })
          .then(resp => {
            localStorage.removeItem('token')
            delete axios.defaults.headers.common['Authorization']
            resolve()
          })
          .catch(err => {
            reject(err)
          })
      })
    }
  }
}