<template>
  <div class="new-project">

    <div class="content">
      <b-field :type="{'is-danger': errors.has('name')}" :message="errors.first('name')">
        <b-input v-model="project.name" v-validate="'required'" name="name" placeholder="Nome do Projeto"></b-input>
      </b-field>

      <b-field label="Descrição">
        <b-input maxlength="150" v-model="project.description" type="textarea"></b-input>
      </b-field>

      <div class="mapSection columns is-mobile">
        <div class="column">
          <b-field label="Mapas">
            <b-select v-model="currentPlan" placeholder="Selecione" expanded>
              <option
                v-for="plan in listPlans"
                :value="plan.id"
                :key="plan.id">
                {{ plan.name }}
              </option>
            </b-select>
          </b-field>
        </div>
        <div class="column is-5">
          <div class="selectPlan" @click="addPlan()">
            <i class="fas fa-plus-circle"></i>
            <div class="labeltext">Selecionar</div>
          </div>
          <div class="createPlan" @click="createPlan()">
            <i class="fas fa-plus-circle"></i>
            <div class="labeltext">Novo Mapa</div>
          </div>
        </div>
      </div>

      <b-field label="Mapas selecionados:" v-if="project.plans && project.plans.length">
        <ul>
        <li
          v-for="plan in project.plans"
            :value="plan.id"
            :key="plan.id">
            {{ plan.name }}
        </li>
        </ul>
      </b-field>

      <div class="has-text-centered">
        <a class="button is-primary" @click="validateSubmit()" :disabled="btDisabled">Proximo</a>
      </div>

    </div>
  </div>
</template>

<script>

export default {
  name: "project-form",
  components: {
  },
  props: {
    saveToStore: {
      type: Boolean,
      default: false
    },
  },
  data() {
    return {
      btDisabled: false,
      project: {
        id: '',
        name: '',
        description: '',
        owner: this.$store.state.auth.user.id,
        plans: []
      },
      currentPlan: '',
      typeAction: '',
      listPlans: [],
    };
  },
  mounted() {
    this.$store.dispatch('getPlans', "owner="+this.$store.getters.getUser.id)
    .then((plans) => {
      this.listPlans = plans
    })

    if (this.$store.getters.getNewProject != null) {
      this.project = this.$store.getters.getNewProject
    }
  },
  methods: {

    addPlan: function () {
      var index = this.listPlans.findIndex(plan => plan.id === this.currentPlan)
      this.project.plans.push(this.listPlans[index])
      this.listPlans.splice(index, 1)
    },

    createProject: function() {
      return new Promise((resolve, reject) => {
        if (this.project.id == null || this.project.id == '') {
          this.$store.dispatch('postProject', this.project)
          .then((project) => {
            this.project = project

            this.$store.dispatch('setNewProject', this.project)

            this.$toast.open({
              message: 'Projeto cadastrado!',
              type: 'is-success'
            })
            resolve(project)
          })
        } else {
          this.project.owner = this.project.owner.id
          this.$store.dispatch('editProject', this.project)
          .then((project) => {
            this.$toast.open({
              message: 'Projeto editado!',
              type: 'is-success'
            })
            resolve(project)
          })
        }
      })
    },

    createPlan(){
      this.$store.dispatch('setNewProject', this.project).then((project)=>{
        this.$router.push({name: 'ProjectPlanForm', params: {saveToStore: true}})
      })
    },

    validateSubmit(){
      this.$validator.validateAll().then(async result => {
        if (result) {
          this.createProject().then((project)=>{
            this.$store.dispatch('setNewProject', project)
            this.$router.push({path: '/app/project/'+project.id})
          })
        } else {
          this.$toast.open({
            message: "Formulário inválido, verifique os campos.",
            type: "is-danger",
            position: "is-bottom"
          });
        }
      })
    }
  }
};
</script>

