<template>
  <div>

    <!-- <file-upload
    @fileSuccess="loadData"
    @queueComplete="loadData"
    :uploadUrl="urlvar"
    :modelId="id"></file-upload> -->


    <vue-dropzone
      id="dropzone"
      :options="dropzoneOptions"
      @vdropzone-queue-complete="(file, xhr, formData) => vqueueComplete(file, xhr, formData)"
      v-on:vdropzone-processing="(file) => processingEvent(file)"
      v-on:vdropzone-sending="sendingEvent"
      @vdropzone-success="(file, response) => successEvent(file, response)"
      ref="myVueDropzone"
      ></vue-dropzone>
  </div>
</template>

<script>
import vue2Dropzone from 'vue2-dropzone'
import 'vue2-dropzone/dist/vue2Dropzone.min.css'
import EXIF from "exif-js"

export default {
  name: 'file-upload',
  components: {
    'vue-dropzone': vue2Dropzone
  },
  props: {
    uploadUrl: {
      type: String
    },
    modelId: {
      type: String
    },
    filename: {
      type: String,
      default: null
    },
  },
  data () {
    return {
      dropzoneOptions: {
        url: 'https://www.googleapis.com/upload/storage/v1/b/storage.vantum.com.br/o?uploadType=media&name=trash',
        uploadMultiple: false,
        dictDefaultMessage: 'Clique aqui ou solte seu orthomosaico aqui para adicionar ao projeto',
        method: 'POST',
        timeout: 0,
        parallelUploads: 1,
        autoProcessQueue: false,
        autoDiscover: false,
        // autoQueue: false,
        accept: function(file, done) {
          const EXIF = require("exif-js")

          EXIF.getData(file, function() {
            if (this.exifdata.GPSLatitude != undefined){
              done();
            }else{
              alert("Atenção: você está enviando uma imagem não georreferenciada, isto comprometerá o promecessamento.")
              done();
            }
          })
          
        },
        headers: {
          'Access-Control-Allow-Origin': '*'
        },
        maxFiles: 500,
        acceptedFiles: 'image/*,image/tiff,image/tif,.tif,.tiff',
        maxFilesize: 2000,
        thumbnailWidth: 35, // px
        thumbnailHeight: 35,
        createImageThumbnails: false,
        previewTemplate: `
        <div class="dz-preview dz-file-preview" style="
          height: 35px;
          width: 35px;
          min-height: 10px;
          margin: 5px 5px;
        ">
          <div class="dz-details" style="
            padding:0;
            opacity: 1;
            background-color: rgba(0, 103, 70, 1);
          ">
            <div class="dz-filename" style="display:none;"><span data-dz-name></span></div>
            <div class="dz-size" style="display:none;" data-dz-size></div>
            <img data-dz-thumbnail />
          </div>
          <div class="dz-progress" style="
            height:10px;
            width:31px;
            opacity: 1;
            z-index: 1000;
            max-width: 80px;
            pointer-events: none;
            position: relative;
            left: 0;
            top: 0;
            margin: 12px 2px;
            background: rgba(255,255,255,0.9);
            -webkit-transform: scale(1);
            border-radius: 8px;
            overflow: hidden;
          "><span class="dz-upload" data-dz-uploadprogress></span></div>
          <div class="dz-success-mark"><span>✔</span></div>
          <div class="dz-error-mark"><span>✘</span></div>
          <div class="dz-error-message"><span data-dz-errormessage></span></div>
        </div>`
      }
    }
  },
  methods: {
    processingEvent (file) {
      this.$refs.myVueDropzone.setOption('headers', { 'Content-Type': file.type })

      // this.$refs.myVueDropzone.setOption('url', 'https://www.googleapis.com/upload/storage/v1/b/storage.vantum.com.br/o?uploadType=multipart&name=orthomosaics/' + orthomosaic.id + '/' + file.name)
      if (this.filename){
        var re = /(?:\.([^.]+))?$/;
        var ext = re.exec(file.name)[1];

        this.$refs.myVueDropzone.setOption('url', this.uploadUrl + this.modelId + '/' + this.filename + "." + ext)

      }else{
        this.$refs.myVueDropzone.setOption('url', this.uploadUrl + this.modelId + '/' + file.name)
      }
    },
    sendingEvent (file, xhr, formData) {
      var _send = xhr.send
      xhr.withCredentials = true
      xhr.setRequestHeader('cache-control', 'no-cache')
      xhr.send = function () {
        _send.call(xhr, file)
      }
    },
    successEvent (file, response) {
      // sucesso de cada arquivo
      this.$emit('fileSuccess', { file, response })
    },
    vqueueComplete (file, xhr, formData) {
      // fila de upload finalizada
      this.$emit('queueComplete', { file, xhr, formData })
    },
    getQueuedFiles () {
      return this.$refs.myVueDropzone.getQueuedFiles().length
    },
    processQueue () {
      this.$refs.myVueDropzone.processQueue()
    }
  }
}
</script>
