<template>
  <div class="billing-view">
    <div class="message">
      <h3>Tudo certo!</h3>
      <p>Agora é só aguardar que entraremos em contato para continuar com o processo de pagamento</p>
      <p>Valor estimado: R$ {{formatPrice(this.calcule_value)}}</p>
    </div>
    <div class="nextButton has-text-centered">
      <a class="button" @click="payAfter()" :disabled="btDisabled">Visualizar</a>
      <!-- <a class="button is-primary" @click="pay()" :disabled="btDisabled">Pagar<i class="fas fa-angle-right"></i> </a> -->
      <!-- <a class="button is-primary" @click="payAfter()" :disabled="btDisabled">Pagar Depois<i class="fas fa-angle-right"></i> </a> -->
    </div>
  </div>
</template>

<script>

  export default {
    name: "billing-view",
    
    data() {
      return {
        btDisabled: false,
        orthomosaic: {
          id: null,
          origem: '',
          amount: '',
          status: '',
          user: '',
          pagamento: '',
        },
        form: {
          fullname: 'juca',
          email: 'leonardorsjoao@gmail.com',
          actuation: null,
          message: 'juca'
        },
        task: {},
        plan: {},
        album: {},
        cost: 0.03709677419,
        value: 0
      }
    },
    components: {
    },
    async mounted () {
      this.task = await this.$store.getters.getNewTask

      await this.$store.dispatch('getPlan', this.task.plan)
      .then((plan) => {
        this.plan = plan
      })

      await this.$store.dispatch('getAlbum', this.task.album)
      .then((album) => {
        this.album = album
      })

      await this.$store.dispatch('postEmailTask', {task: this.task.id, value: this.calcule_value})
      .then(() => {
        this.$toast.open({
          message: 'Email enviado!',
          type: 'is-success'
        })
      })
    },
    computed: {
      calcule_value () {
        // console.log("task564654", this.taskId)
        return this.album.count * this.cost * ((this.album.width * this.album.height)/1000000)
      }
    },
    methods: {
      formatPrice(value) {
        let val = (value/1).toFixed(2).replace('.', ',')
        return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
      },
      payAfter() {
        if(this.plan.project != null){
          this.$router.push({path: '/app/project/'+this.plan.project.id, params:{projectId:this.plan.project.id}})
        } else{
          this.$router.push({path: '/app/plan/'+this.plan.id, params:{planId:this.plan.id}})          
        }
      },
    },
  }
</script>

