    margin-top: 20px;

    .message{
      text-align: center;
      font-weight: bold;
      padding: 5px;

      h3{
        font-weight: bold;
        font-size: 20px;
        margin-bottom: 20px;
      }
    }

    .nextButton{
      .button{
        color: white;
        background-color: $color-light;
      }
    }
  }
</style>