<template>
  <div class="plan">
    <b-loading :is-full-page="false" :active.sync="isLoading" :can-cancel="false"></b-loading>

    <navigation-menu v-if="plan && !isLoading" :title="plan.name" :arrowBack="arrowBack">
        <div class="deleteButton" v-on:click.stop="confirmCustomDelete()" v-if="!example && arrowBack">
          <i class="fas fa-trash"></i>
        </div>
      </navigation-menu>

    <plan-details v-if="plan && !isLoading" :plan="plan"></plan-details>
    <div class="errorNotFound" v-if="error && !isLoading">
      Desculpe, este mapa não foi encontrado.<br /> :(
    </div>
  </div>
</template>

<script>
  import PlanDetails from  './PlanDetails.vue'
  import NavigationMenu from "../NavigationMenu";

  export default {
    name: "plan",
    components:{
      NavigationMenu,
      "plan-details": PlanDetails
    },
    props: {
      planId: {
        type: String
      },
      share: {
        type: Object
      },
      example: {
        type: Boolean,
        default: false
      },
    },
    data() {
      return {
        plan: null,
        error: false,
        isLoading: false,
        arrowBack: true
      }
    },

     mounted () {
      this.isLoading = true
      if(this.share && this.share.linkSharing){
        this.arrowBack = false
      }
      this.$store.dispatch('getPlan', this.planId)
      .then( async (plan) => {
        this.plan = plan
        
        // this.plan.name = plan.name
        // this.plan.tasks = plan.tasks
        
        if(plan.orthomosaics != null){
          plan.orthomosaics.forEach((orthomosaic, index) => {
            if (index == 0){
              orthomosaic.selected = true
            }else{
              orthomosaic.selected = false
            }
            orthomosaic.opacity = 1
          });
          this.plan.orthomosaics = plan.orthomosaics
        }
        else{
          this.plan.orthomosaics = []
        }

        if(plan.annotations != null){
          plan.annotations.forEach(annotation => {
            annotation.selected = true
          });
          this.plan.annotations = plan.annotations
        }
        else{
          this.plan.annotations = []
        }

        await this.$store.dispatch('setOrthomosaics', plan.orthomosaics)

        // this.$store.dispatch('toogleOrthomosaic', plan.orthomosaics[0])
        if (this.$store.getters.getOrthomosaics.length){
          var location = {
            altitude: 14,
            latitude: this.$store.getters.getOrthomosaics[0].image.location.latitude,
            longitude: this.$store.getters.getOrthomosaics[0].image.location.longitude
          }
          
          this.centerMap(location)
          this.$root.$emit('activateOrthomosaics', this.$store.getters.getOrthomosaics[0])
          this.$store.dispatch('setActiveOrthomosaics', this.$store.getters.getOrthomosaics[0])

        }

        this.isLoading = false


      })
      .catch(()=>{
        this.isLoading = false
        this.error = true
      })
          
    },

    methods: {
      centerMap(coords) {
        this.$root.$emit('centerMap', coords)
      },
      deleteItem() {
        if(!this.share){
          this.$store.dispatch('deletePlan', this.planId)
          .then( () => {
            this.$notify({
              group: 'global',
              type: 'is-success',
              duration: 2000,
              title: 'Mapa removido'
            });
          })
        }
        else {
          this.$store.dispatch('deleteSharing', this.share.id)
          .then( () => {
            this.$notify({
              group: 'global',
              type: 'is-success',
              duration: 2000,
              title: 'Projeto compartilhado removido'
            });
          })
        }        
      },

      confirmCustomDelete() {
        this.$dialog.confirm({
          title: 'Deletando Mapa',
          message: 'Você tem certeza que deseja <b>deletar</b> o mapa ? Isso é uma ação sem volta.',          
          confirmText: 'Deletar Mapa',
          type: 'is-danger',
          hasIcon: true,
          onConfirm: () => this.deleteItem()
        })
      },
    },
  }
</script>

<style lang="scss" scope>
  .plan{
    width: 100%;
    padding: 10px;
    overflow-x: hidden;
    overflow-y: auto;

    &::-webkit-scrollbar-track {
      background-color: #f5f5f5;
    }

    &::-webkit-scrollbar {
      width: 8px;
      background-color: #f5f5f5;
    }

    &::-webkit-scrollbar-thumb {
      // border-radius: 10px;
      background-color: #888;
    }

    .errorNotFound{
      text-align: center;
      font-weight: bold;
      margin-top: 20px;
    }
  }

</style>