<template>
  <div class="album-form">
    <div class="content">
      <!-- <b-field label="Criando novo Album"></b-field> -->

      <b-field
        label="Nome"
        :type="{'is-danger': errors.has('name')}"
        :message="errors.first('name')"
      >
        <b-input
          v-model="album.name"
          v-validate="'required'"
          name="name"
          placeholder="Nome do Album"
        ></b-input>
      </b-field>

      <div class="columns is-mobile">
        <div class="column">
          <b-field
            label="Tipo de Camera"
            :type="{'is-danger': errors.has('CameraType')}"
            :message="errors.first('CameraType')"
          >
            <b-select
              placeholder="Selecione"
              expanded
              v-model="album.camera"
              v-validate="'required'"
              name="CameraType"
            >
              <option
                v-for="typeCam in listCameraType"
                :value="typeCam.name"
                :key="typeCam.id"
              >{{ typeCam.name }}</option>
            </b-select>
          </b-field>
        </div>
        <div class="column">
          <b-field
            label="Tipo de Imagens"
            :type="{'is-danger': errors.has('AlbumType')}"
            :message="errors.first('AlbumType')"
          >
            <b-select
              placeholder="Selecione"
              expanded
              v-model="album.type"
              v-validate="'required'"
              name="AlbumType"
            >
              <option
                v-for="typeAlbum in listAlbumType"
                :value="typeAlbum.name"
                :key="typeAlbum.id"
              >{{ typeAlbum.name }}</option>
            </b-select>
          </b-field>
        </div>
      </div>

      <b-field label="Descrição">
        <b-input maxlength="150" v-model="album.description" type="textarea"></b-input>
      </b-field>

      <h6>Envie suas imagens</h6>
      <div class="georefWarning warning">Atenção, envie apenas imagens georreferenciadas.</div> 
      <file-upload
        @fileSuccess="(p) => successEvent(p.file, p.response)"
        @queueComplete="(p) => vqueueComplete(p.file, p.xhr, p.formData, this.album)"
        :uploadUrl="'https://www.googleapis.com/upload/storage/v1/b/storage.vantum.com.br/o?uploadType=multipart&name=user_data/'+userFolder+'/albums/'"
        :modelId="album.id"
        ref="fileUpload"
      ></file-upload>

      <div class="nextButton has-text-centered" v-if="showBt">
        <a class="button is-primary" @click="validateSubmit()" :disabled="btDisabled">
          Próximo
          <i class="fas fa-angle-right"></i>
        </a>
      </div>
    </div>

    <!-- <router-link :to="{ name: 'PlanForm'}" class="selector">
      <i class="fas fa-share-alt"></i>
    </router-link>-->
  </div>
</template>

<script>
import NavigationMenu from "../NavigationMenu";
import FileUpload from "../FileUpload";
import EXIF from "exif-js"

export default {
  name: "album-form",
  components: {
    NavigationMenu,
    "file-upload": FileUpload
  },
  props: {
    showBt: {
      type: Boolean,
      default: true
    },
  },
  data() {
    return {
      btDisabled: false,
      plan: null,
      project: null,
      task: {
        id: null,
        job: {orthomosaic: true},
        status: "waiting"
      },
      album: {
        id: null,
        name: '',
        description: '',
        altitude: 0,
        width: 0,
        height: 0,
        type: '',
        count: 0,
        owner: this.$store.state.auth.user.id,
        camera: ''
      },

      listCameraType: [{id: '1', name: 'RGB'}],
      listAlbumType: [{id: '1', name: 'Urbano'}, {id: '2', name: 'Rural'}, {id: '3', name: 'Floresta'}],
    };
  },

  computed: {
    userFolder(){
      return this.$store.state.auth.user.email+"_"+this.$store.state.auth.user.id
    }
  },
    
  mounted() {

  },
  methods: {
    successEvent (file, response) {
      var self = this

      EXIF.getData(file, function() {
        var latitude_deg =  this.exifdata.GPSLatitude[0].numerator/this.exifdata.GPSLatitude[0].denominator
        var latitude_minute = this.exifdata.GPSLatitude[1].numerator/this.exifdata.GPSLatitude[1].denominator
        var latitude_second = this.exifdata.GPSLatitude[2].numerator/this.exifdata.GPSLatitude[2].denominator

        var longitude_deg =  this.exifdata.GPSLongitude[0].numerator/this.exifdata.GPSLongitude[0].denominator
        var longitude_minute = this.exifdata.GPSLongitude[1].numerator/this.exifdata.GPSLongitude[1].denominator
        var longitude_second = this.exifdata.GPSLongitude[2].numerator/this.exifdata.GPSLongitude[2].denominator

        var altitude = this.exifdata.GPSAltitude.numerator/this.exifdata.GPSAltitude.denominator
        var latitude = latitude_deg + latitude_minute/60 + latitude_second/3600
        var longitude = longitude_deg + longitude_minute/60 + longitude_second/3600

        if(this.exifdata.GPSLatitudeRef == 'S'){
          latitude = latitude * -1
        }
        if(this.exifdata.GPSLongitudeRef == 'W'){
          longitude = longitude * -1
        }

        var image = {
          filename: file.upload.filename,
          size: file.size,
          resolution: (this.exifdata.PixelXDimension * this.exifdata.PixelYDimension) / 1000000, 
          width: this.exifdata.PixelXDimension,
          height: this.exifdata.PixelYDimension,
          type: file.type,
          url: response.mediaLink,
          location: {'altitude': altitude, 'latitude': latitude, 'longitude': longitude},
          thumbmail_url: "https://www.googleapis.com/download/storage/v1/b/storage.vantum.com.br/o/user_data%2F"+self.userFolder+"%2Falbums%2F"+self.album.id+"%2Fthumbnails%2F"+file.upload.filename,
          album: self.album.id
        }

        self.$store.dispatch('postImage', image)
      })

    },
    vqueueComplete (file, xhr, formData, album) {
      this.queueComplete = true
      this.$toast.open({
        message: 'Upload completo',
        type: 'is-success'
        // position: 'is-bottom'
      })
      // console.log(album)
      // this.$store.dispatch('editPlan', {id: this.planId, album:album.id})
      
      // this.$store.dispatch('postTask', {job:"['orthomosaic']", plan:this.planId, album:album.id, status: "waiting"})

      if(this.plan != null){
        if(this.project != null){
          this.$router.push({path: '/app/project/'+ this.project.id + '/billing'})
        } else{
          this.$router.push({path: '/app/plan/'+ this.plan.id + '/billing'})
        }
      }
      else {
        this.$router.push({path: '/app/album/'+this.album.id})
      }
    },
    
    createTask: function() {
      return new Promise((resolve, reject) => {
        if (this.$store.getters.getNewTask != null){
          this.$store.dispatch('postTask', this.$store.getters.getNewTask)
          .then((task) => {
            // console.log(task)
            this.task = task
            resolve(task)
          })
        }
        else{
          resolve(null)
        }
      })
    },
    createAlbum: function() {
      return new Promise((resolve, reject) => {
        if (this.$store.getters.getNewAlbum != null){
          this.$store.dispatch('postAlbum', this.$store.getters.getNewAlbum)
          .then((album) => {
            this.album = album
            resolve(album)
          })
        }else{
          resolve(null)
        }
      })
    },
    createProject: function() {
      return new Promise((resolve, reject) => {
        if (this.$store.getters.getNewProject != null){
          this.$store.dispatch('postProject', this.$store.getters.getNewProject)
          .then((project) => {
            this.project = project
            resolve(project)
          })
        }else{
          resolve(null)
        }
      })
    },
    createPlan: function() {
      return new Promise((resolve, reject) => {
        if (this.$store.getters.getNewPlan != null){
          this.$store.dispatch('postPlan', this.$store.getters.getNewPlan)
          .then((plan) => {
            this.plan = plan
            resolve(plan)
          })
        }else{
          resolve(null)
        }
      })
    },

    validateSubmit() {
      return new Promise((resolve, reject) => {
        if (!this.btDisabled) {
          this.$validator.validateAll().then(async result => {
            if (result) {
              if (this.$refs.fileUpload.getQueuedFiles() > 0) {
              
                this.btDisabled = true
                
                await this.$store.dispatch('setNewAlbum', this.album)
                
                var newPlan = await this.$store.getters.getNewPlan

                await this.createProject().then((project)=>{
                  if (project != null && newPlan != null){
                    newPlan.project = project.id
                  }
                })

                await this.createAlbum().then((album)=>{
                  if (album != null && newPlan != null){
                    newPlan.album = album.id
                  }
                  this.task.album = album.id
                })

                newPlan = await this.$store.dispatch('setNewPlan', newPlan)

                await this.createPlan().then((plan)=>{
                  this.plan = plan
                  if (plan != null){
                    this.task.plan = plan.id
                  }
                })

                await this.$store.dispatch('setNewTask', this.task)

                // console.log("TASK 1",this.task)

                await this.createTask().then((task)=>{
                  this.task = task
                  this.$store.dispatch('setNewTask', task)
                })

                this.$refs.fileUpload.processQueue()

                this.$toast.open({
                  message: 'Upload em andamento',
                  type: 'is-success'
                  // position: 'is-bottom'
                })

                // await this.createProject().then((project)=>{
                //   this.createAlbum().then((album)=>{
                //     var newPlan = this.$store.getters.getNewPlan
                //     if (project != null){
                //       newPlan.project = project.id
                //     }
                //     if (album != null){
                //       newPlan.album = album.id
                //     }
                //     this.$store.dispatch('setNewPlan', newPlan).then((plan)=>{
                //       this.createPlan().then((plan)=>{
                //         console.log("oi")
                //         this.$refs.fileUpload.processQueue()
        
                //         this.$toast.open({
                //           message: 'Upload em andamento',
                //           type: 'is-success'
                //           // position: 'is-bottom'
                //         })
                        
                //       })
                //     })
                //   })
                // })
              } else {
                this.$toast.open({
                  message: 'Album não adicionado, necessário haver mais de uma imagem',
                  type: 'is-danger'
                  // position: 'is-bottom'
                })
              }

              return ;
            }
            this.$toast.open({
              message: "Formulário inválido, verifique os campos.",
              type: "is-danger",
              position: "is-bottom"
            });
          });
        }
      })
    }
  }
};
</script>

<style lang="scss">
.nextButton {
  margin-bottom: 50px;
  a {
    i {
      margin-left: 10px;
      // font-size: 120%;
    }
  }
}
