<template>
	<div class="dashboard">
    <div class="appMap">
      <l-map
      :key="componentKey"
      :zoom="zoom"
      :center="center"
      :options="mapOptions"
      :max-zoom="maxZoom"
      @update:center="centerUpdate"
      @update:zoom="zoomUpdate"
      ref="myMap"
    >
      <l-tile-layer
        :url="url"
        :options="{ maxZoom: 20 }"
      />
      <!-- <l-image-overlay
        :url="urlO"
        :bounds="bounds"/>  -->
      <l-tile-layer
        v-for="(orthomosaic, index) in computedOrthomosaics"
        :url="orthomosaic.tilesUrl"
        :tms="true"
        :key="orthomosaic.id"
        :options="{ maxZoom: 20 }"
        :opacity="parseFloat(orthomosaic.opacity)"
      />
    </l-map>
    </div>
    <sidenav></sidenav>
    
  </div>
</template>

<script>
import Sidenav from '@/components/app/Sidenav'
import { LMap, LImageOverlay, LTileLayer, LMarker, LPopup, LTooltip, LControlLayers } from 'vue2-leaflet';

export default {
  name: "dashboard",
  data() {
    return {
      orthomosaics: [],
      search: '',
      op: 1,
      blakey: 0,
      componentKey: 0,
      maxZoom: 20,
      projects: this.$store.state.app.projects,
      zoom: 4, // não usado, ver computed mapZoom
      center: L.latLng(-13.0618256, -63.5501318),// não usado, ver computed mapCenter
      url: 'http://mt1.google.com/vt/lyrs=y&x={x}&y={y}&z={z}',
      tileUrl: 'http://localhost:1337/odm_orthophoto/{z}/{x}/{y}.png',
      // bounds: [[-31.8870864349, -52.6375153957], [-31.875783158, -52.6497473174]],
      mapOptions: {
        zoomSnap: 0.5
      }
    };
  },
  methods: {
    zoomUpdate (zoom) {
      // this.currentZoom = zoom;
    },
    centerUpdate (center) {
      // this.currentCenter = center;
    },
    showLongText () {
      this.showParagraph = !this.showParagraph;
    },
    innerClick () {
      alert('Click!');
    },
  },
  computed: {
    computedOrthomosaics(){
      return this.orthomosaics
      // return this.$store.getters.getOrthomosaics
    },
  },
  components: {
    Sidenav,
    LMap,
    LTileLayer,
    LMarker,
    LPopup,
    LImageOverlay,
    LTooltip,
    LControlLayers
  },
  mounted () {
    this.$root.$on('centerMap', (coords) => {
      this.$refs.myMap.setZoom(coords.altitude)
      this.$refs.myMap.setCenter([coords.latitude, coords.longitude])
    })

    // this.$root.$on('updateOrthomosaics', (ref) => {
    //   this.orthomosaics = ref
    // })

    this.$root.$on('updateOpacity', (ref) => {
      var index = this.orthomosaics.findIndex(orthomosaic => orthomosaic.id === ref.id)
      this.orthomosaics[index].opacity = ref.opacity
      var o = this.orthomosaics[index]
      this.orthomosaics.splice(index, 1)
      this.orthomosaics.push(o)
    })

    this.$root.$on('activateOrthomosaics', (ref) => {
      var index = this.orthomosaics.findIndex(orthomosaic => orthomosaic.id === ref.id)
      if (index == -1){
        this.orthomosaics.push(ref)
      }else{
        if (ref.selected == this.orthomosaics[index].selected){
          this.orthomosaics.splice(index, 1)
        }else{
          this.orthomosaics[index] = ref
        }
      }
    })

    this.$nextTick(() => {
      // this.$refs.myMap.mapObject.ANY_LEAFLET_MAP_METHOD();
    })
  },
};
</script>

