          {{ plan.createdAt | moment("DD/MM/YYYY HH:mm")}}
        </div>

        <div class="owner">
          <i class="fas fa-user-alt"></i>
          {{ plan.owner.firstName }}
        </div>
      </div>

      <div class="status">
        Status:
        <strong>{{ getStatus }}</strong>
      </div>

      <orthomosaic-selector :key="orthomosaicsKey"></orthomosaic-selector>

      <b-collapse class="tools" :open.sync="openTools">
        <div
          slot="trigger"
          class="header"
          role="button"
        >
          <p class="title">Ferramentas</p>
          <a class="icon">
            <i class="fas" :class="openTools ? 'fa-angle-down' : 'fa-angle-right'"></i>
          </a>
        </div>
        <div class="content">
          <div class="tool-box columns is-mobile">
            <div class="column tool active" @click="selectTool('tool')"><i class="fas fa-map-marker-alt"></i></div>
            <div class="column tool" @click="selectTool('tool')"><i class="fas fa-draw-polygon"></i></div>
            <div class="column tool" @click="selectTool('tool')"><i class="fas fa-ruler"></i></div>
            <div class="column tool" @click="selectTool('tool')"><i class="fas fa-cubes"></i></div>
          </div>
          <div class="annotations">

            <div class="annotation"
              v-for="annotation in plan.annotations"
              @click="selectAnnotation(annotation)"
              :class="{ 'active': annotation.selected == true}"
              :style="annotation.selected == true ? style_bgcolor(annotation.color) : ''"
              :key="annotation.id"
            >
              <div class="icon" :style="style_bgcolor(annotation.color)">
                <i class="fas" :class="annotationClassIcon(annotation.type)"></i>
              </div>
              <div class="description">{{annotation.description}}</div>
              <div class="data"></div>
              <div class="deleteButton" v-on:click.stop="removeAnnotation(annotation)">
                <i class="fas fa-trash"></i>
              </div>
            </div>

          </div>
        </div>
      </b-collapse>

      <b-collapse class="album" v-if="plan.album" :open.sync="openAlbum">
        <div
          slot="trigger"
          class="header"
          role="button"
        >
          <p class="title">Album/Dataset</p>
          <a class="icon">
            <i class="fas" :class="openAlbum ? 'fa-angle-down' : 'fa-angle-right'"></i>
          </a>
        </div>
        <div class="content">
            <album-thumbnails :album="plan.album"></album-thumbnails>
          </div>
        <!-- <div class="content">
