  </div>
</template>

<script>
import NavigationMenu from "../NavigationMenu";
import AlbumThumbnails from "../album/AlbumThumbnails"
import OrthomosaicSelector from "./OrthomosaicSelector"

export default {
  name: "plan-details",
  props: {
    plan: {
      type: Object,
    },
  },
  data() {
    return {
      openTools: false,
      openAlbum: false,
      album: {},
      orthomosaicsKey: 0
    };
  },
  components: {
    NavigationMenu,
    "album-thumbnails": AlbumThumbnails,
    "orthomosaic-selector": OrthomosaicSelector
  },
  mounted() {
    this.$root.$on('toggleOrthomosaics', () => {
      this.orthomosaicsKey++
    })
    // this.$store.dispatch('getAlbum', this.plan.album)
    // .then((album) => {
    //   this.album = album
    // })
    // console.log(this.plan)
    // if (this.plan.orthomosaics != []){
    //   this.$store.dispatch('setCurrentOrthomosaic', this.plan.orthomosaics[0])
    // }
  },
  methods: {
    centerMap(coords) {
      this.$root.$emit('centerMap', coords)
    },
    update(val) {
      this.value = Number(val);
    },
    style_bgcolor(hex){
      return "background-color: " + hex
    },
    annotationClassIcon(type){
      switch (type) {
        case "marker":
          return "fa-map-marker-alt";
        case "line":
          return "fa-draw-polygon";
        case "polygon":
          return "fa-ruler";
        case "volume":
          return "fa-cubes";
      }
    },
    selectTool(tool){
      tool.selected = !tool.selected
    },
    selectAnnotation(annotation){
      annotation.selected = !annotation.selected
    },
    removeAnnotation(annotation){
      // annotation.selected = !annotation.selected
    },
  },
  computed: {
    orthomosaics: {
      get() {
        return this.$store.getters.getOrthomosaics
      },
      set(value) {
        this.$store.dispatch('setOrthomosaics', value);
      },
    },
    getStatus() {
      if(this.plan.tasks != undefined){
        switch (this.plan.tasks[0].status) {
          case "waiting":
            return "Aguardando";
          case "waiting_payment":
            return "Aguardando pagamento";
          case "tiling":
            return "Otimizando";
          case "processing":
            return "Processando";
          case "initializing":
            return "Iniciando processamento";
          case "completed":
            return "Completo";
          default:
            return "Aguardando";
        }
      } else {
            return "Aguardando";
      }
    }
  }
};
</script>

