<template>
  <div class="orthomosaic-form">
    <div class="content">
      <b-field label="Enviando novo Ortomosaico"></b-field>

      <div class="georefWarning warning">Atenção, envie apenas imagens georreferenciadas.</div> 
      <file-upload
        @fileSuccess="(p) => successEvent(p.file, p.response)"
        @queueComplete="(p) => vqueueComplete(p.file, p.xhr, p.formData, this.orthomosaic)"
        :uploadUrl="'https://www.googleapis.com/upload/storage/v1/b/storage.vantum.com.br/o?uploadType=multipart&name=user_data/'+userFolder+'/orthomosaics/'"
        :modelId="orthomosaic.id"
        filename="orthophoto"
        ref=fileUpload>
      </file-upload>
      
      <div class="nextButton has-text-centered">
        <a class="button is-primary" @click="validateSubmit()" :disabled="btDisabled">Próximo <i class="fas fa-angle-right"></i></a>
      </div>
      <div class="finishButton has-text-centered" v-if="completed">
        <a class="button is-primary" @click="finish()" :disabled="btDisabled">Concluir<i class="fas fa-angle-right"></i></a>
      </div>
    </div>
  </div>
</template>

<script>
  import FileUpload from "../FileUpload";
  import EXIF from "exif-js"
  // import { Store } from 'tough-cookie';

  export default {
    name: "orthomosaic-form",
    data() {
      return {
        completed: false,
        btDisabled: false,
        plan: null,
        project: null,
        image: null,
        task: {
          job: {
            tile: true
          },
          status: "waiting"
        },
        orthomosaic: {
          id: null,
          type: "orthophoto",
          tilesUrl: "",
          plan: null,
          image: null,
          task: null
        }
      }
    },
    props: {
      planId: {
        type: String,
      },
    },
    components: {
      "file-upload": FileUpload
    },
    mounted () {
    },
    computed: {
      userFolder(){
        return this.$store.state.auth.user.email+"_"+this.$store.state.auth.user.id
      }
    },
    methods: {
      createTask: function() {
        return new Promise((resolve, reject) => {
          if (this.$store.getters.getNewTask != null){
            this.$store.dispatch('postTask', this.$store.getters.getNewTask)
            .then((task) => {
              this.task = task
              resolve(task)
            })
          }else{
            resolve(null)
          }
        })
      },
      createAlbum: function() {
        return new Promise((resolve, reject) => {
          if (this.$store.getters.getNewAlbum != null){
            this.$store.dispatch('postAlbum', this.$store.getters.getNewAlbum)
            .then((album) => {
              this.album = album
              resolve(album)
            })
          }else{
            resolve(null)
          }
        })
      },
      createProject: function() {
        return new Promise((resolve, reject) => {
          if (this.$store.getters.getNewProject != null){
            this.$store.dispatch('postProject', this.$store.getters.getNewProject)
            .then((project) => {
              this.project = project
              resolve(project)
            })
          }else{
            resolve(null)
          }
        })
      },
      createPlan: function() {
        return new Promise((resolve, reject) => {
          if (this.$store.getters.getNewPlan != null){
            this.$store.dispatch('postPlan', this.$store.getters.getNewPlan)
            .then((plan) => {
              this.plan = plan
              resolve(plan)
            })
          }else{
            resolve(null)
          }
        })
      },

      createOrthomosaic () {
        return new Promise((resolve, reject) => {
          if (this.$store.getters.getNewOrthomosaic != null){
            this.$store.dispatch('postOrthomosaic', this.$store.getters.getNewOrthomosaic)
            .then((orthomosaic) => {
              this.orthomosaic = orthomosaic
              resolve(orthomosaic)
            })
          }else{
            resolve(null)
          }
        })
      },

      updateOrthomosaic () {
        return new Promise((resolve, reject) => {
          if (this.$store.getters.getNewOrthomosaic != null){
            this.$store.dispatch('editOrthomosaic', this.$store.getters.getNewOrthomosaic)
            .then((orthomosaic) => {
              this.orthomosaic = orthomosaic
              resolve(orthomosaic)
            })
          }else{
            resolve(null)
          }
        })
      },

      successEvent (file, response) {
        var self = this

        EXIF.getData(file, async function() {

          if (this.exifdata.GPSLatitude){
            var latitude_deg =  this.exifdata.GPSLatitude[0].numerator/this.exifdata.GPSLatitude[0].denominator
            var latitude_minute = this.exifdata.GPSLatitude[1].numerator/this.exifdata.GPSLatitude[1].denominator
            var latitude_second = this.exifdata.GPSLatitude[2].numerator/this.exifdata.GPSLatitude[2].denominator
  
            var longitude_deg =  this.exifdata.GPSLongitude[0].numerator/this.exifdata.GPSLongitude[0].denominator
            var longitude_minute = this.exifdata.GPSLongitude[1].numerator/this.exifdata.GPSLongitude[1].denominator
            var longitude_second = this.exifdata.GPSLongitude[2].numerator/this.exifdata.GPSLongitude[2].denominator
  
            var altitude = this.exifdata.GPSAltitude.numerator/this.exifdata.GPSAltitude.denominator
            var latitude = latitude_deg + latitude_minute/60 + latitude_second/3600
            var longitude = longitude_deg + longitude_minute/60 + longitude_second/3600
  
            if(this.exifdata.GPSLatitudeRef == 'S'){
              latitude = latitude * -1
            }
            if(this.exifdata.GPSLongitudeRef == 'W'){
              longitude = longitude * -1
            }

            // D + M/60 + S/3600
            self.image = {
              filename: file.upload.filename,
              size: file.size,
              resolution: (this.exifdata.PixelXDimension * this.exifdata.PixelYDimension) / 1000000, 
              width: this.exifdata.PixelXDimension,
              height: this.exifdata.PixelYDimension,
              type: file.type,
              url: response.mediaLink,
              location: {'altitude': altitude, 'latitude': latitude, 'longitude': longitude},
              thumbmail_url: "",
              album: null
            }
          }else{
            // D + M/60 + S/3600
            self.image = {
              filename: file.upload.filename,
              size: file.size,
              resolution: 0, 
              width: 0,
              height: 0,
              type: file.type,
              url: response.mediaLink,
              location: {},
              thumbmail_url: "",
              album: null
            }
          }

          await self.$store.dispatch('postImage', self.image).then((image)=>{
            self.orthomosaic.image = image.id
          })


        })
      },

      async vqueueComplete (file, xhr, formData, orthomosaic) {
        this.queueComplete = true
        this.$toast.open({
          message: 'Upload completo',
          type: 'is-success'
        })

        // await this.$store.dispatch('setNewOrthomosaic', this.orthomosaic)

        // await this.createOrthomosaic().then((orthomosaic)=>{
        //   this.orthomosaic = orthomosaic
        //   this.task.orthomosaic = orthomosaic.id
        // })

        await this.$store.dispatch('setNewTask', this.task)

        await this.createTask().then((task)=>{
          this.task = task
          this.orthomosaic.task = task.id
        })

        await this.$store.dispatch('setNewOrthomosaic', this.orthomosaic)

        await this.updateOrthomosaic().then((orthomosaic)=>{
          this.orthomosaic = orthomosaic
        })

        this.completed = true
        
        if(this.plan != null){
          if(this.project != null){
            this.$router.push({path: '/app/project/'+ this.project.id, params:{planId: this.plan.id, projectId: this.project.id, taskId: this.task.id}})
          } else{
            this.$router.push({path: '/app/plan/'+ this.plan.id, params:{planId: this.plan.id, taskId: this.task.id}})
          }
        }
        else {
          this.$router.push({path: '/app/album/'+this.album.id})
        }
      },

      finish(){
        if(this.plan != null){
          if(this.project != null){
            this.$router.push({path: '/app/project/'+ this.project.id, params:{planId: this.plan.id, projectId: this.project.id, taskId: this.task.id}})
          } else{
            this.$router.push({path: '/app/plan/'+ this.plan.id, params:{planId: this.plan.id, taskId: this.task.id}})
          }
        }
        else {
          this.$router.push({path: '/app/album/'+this.album.id})
        }
      },

      validateSubmit() {
        if (!this.btDisabled) {
          this.$validator.validateAll().then(async result => {
            if (result) {
              if (this.$refs.fileUpload.getQueuedFiles() > 0) {
                
                this.btDisabled = true
                
                await this.$store.dispatch('setNewOrthomosaic', this.orthomosaic)

                var newPlan = await this.$store.getters.getNewPlan

                await this.createProject().then((project)=>{
                  if (project != null && newPlan != null){
                    newPlan.project = project.id
                  }
                })

                newPlan = await this.$store.dispatch('setNewPlan', newPlan)

                await this.createPlan().then((plan)=>{
                  this.plan = plan
                  this.orthomosaic.plan = plan.id
                  this.task.plan = plan.id
                })

                await this.$store.dispatch('setNewOrthomosaic', this.orthomosaic)

                await this.createOrthomosaic().then((orthomosaic)=>{
                  this.orthomosaic = orthomosaic
                  this.task.orthomosaic = orthomosaic.id
                })

                this.$refs.fileUpload.processQueue()
        
                this.$toast.open({
                  message: 'Upload em andamento, aguarde',
                  type: 'is-success'
                })

              } else {
                this.btDisabled = false

                this.$toast.open({
                  message: 'Nenhum arquivo para enviar',
                  type: 'is-danger'
                })
              }

            }else{
              this.$toast.open({
                message: "Formulário inválido, verifique os campos.",
                type: "is-danger",
                position: "is-bottom"
              });
            }
          });
        }
      }
    },
  }
</script>

<style lang="scss" scoped>
  .orthomosaic-form{
    .georefWarning{
      font-size: 13px;
      color: #666;
      padding: 5px;
      text-align: center;
      background-color: #FCD12A;
      font-weight: bold;
    }
  }
</style>