<template>
  <div class="album-form">

    <div class="content">
      <b-field :type="{'is-danger': errors.has('name')}" :message="errors.first('name')">
        <b-input v-model="plan.name" v-validate="'required'" name="name" placeholder="Nome do Mapa (ex:Mapa-lugar)"></b-input>
      </b-field>

      <b-field label="Tipo de Processamento" :type="{'is-danger': errors.has('ProcessType')}" :message="errors.first('ProcessType')">
        <b-select placeholder="Selecione" v-model="task.job" v-validate="'required'" name="ProcessType">
          <option
            v-for="(typeProcess, index) in listProcessType"
            :value="typeProcess.id"
            :key="typeProcess.id">
            {{ typeProcess.name }}
          </option>
        </b-select>
      </b-field>

      <div class="nextButton has-text-centered">
        <a class="button is-primary" @click="validateSubmit('dataset')" :disabled="btDisabled">Selecionar ou Enviar Imagens<i class="fas fa-angle-right"></i> </a>        
        <a class="button is-primary" @click="validateSubmit('orthomosaic')" :disabled="btDisabled">Enviar Ortomosaico processado<i class="fas fa-angle-right"></i> </a>
      </div>

    </div>
    <!-- <router-link :to="{ name: 'AlbumForm'}" class="selector">
      <i class="fas fa-share-alt"></i>
    </router-link> -->

  </div>
</template>

<script>

export default {
  name: "album-form",
  components: {
  },
  props: {
    albumId: {
      type: String,
    },
    projectId: {
      type: String,
    },
    saveToStore: {
      type: Boolean,
      default: false
    },
  },
  data() {
    return {
      btDisabled: false,
      plan: {
        id: '',
        name: '',
        location: '',
        album: this.albumId,
        owner: this.$store.state.auth.user.id,
        project: null,
      },
      task: {
        job: {orthomosaic: true},
        status: "waiting",
        plan: null,
        album: null, 
        orthomosaic: null
      },
      listProcessType: [
        {id: "['orthomosaic']", name: 'Padrão'},
      ],
    };
  },
  mounted() {
    if (this.$store.getters.getNewPlan != null) {
      this.plan = this.$store.getters.getNewPlan
    }
    if (this.$store.getters.getNewTask != null) {
      this.task = this.$store.getters.getNewTask
    }
  },
  methods: {
    createPlan: function() {
      return new Promise((resolve, reject) => {
        if (this.$store.getters.getNewPlan == null) {
          this.$store.dispatch('postPlan', this.plan)
          .then((plan) => {
            this.plan = plan

            this.$store.dispatch('setNewPlan', this.plan)

            this.$toast.open({
              message: 'Mapa cadastrado!',
              type: 'is-success'
            })
            resolve(plan)
          })
        } else {
          this.plan.owner = this.plan.owner.id
          this.$store.dispatch('editPlan', this.plan)
          .then((plan) => {
            this.$toast.open({
              message: 'Mapa editado!',
              type: 'is-success'
            })
            resolve(plan)
          })
        }
      })
    },
    validateSubmit(redirect) {
      this.$validator.validateAll().then(async result => {
        if (result) {
          this.$store.dispatch('setNewPlan', this.plan)
          this.$store.dispatch('setNewTask', this.task)

          var newProject = this.$store.getters.getNewProject

          if(redirect == 'dataset'){
            if (newProject == null){
              this.$router.push({name: 'PlanAlbumForm', params: {saveToStore: true}})
            }else{
              this.$router.push({name: 'ProjectAlbumForm', params: {saveToStore: true}})
            }
          } else{
            if (newProject == null){
              this.$router.push({name: 'PlanOrthomosaicForm', params: {saveToStore: true}})
            }else{
              this.$router.push({name: 'ProjectOrthomosaicForm', params: {saveToStore: true}})
            }
          }
          // if (this.saveToStore == false){
          //   await this.createPlan()
          //   .then((plan)=>{
  
          //     if(redirect == 'dataset'){
          //       this.$router.push({name: 'AlbumForm', params: {projectId: this.projectId, planId: plan.id, taskType: this.taskJob}})
          //     } else{
          //       this.$router.push({name: 'OrthomosaicForm', params: {planId: plan.id}})
          //     }
  
          //   })
          //   .catch(()=>{
          //     this.$toast.open({
          //       message: 'Mapa não adicionado',
          //       type: 'is-danger'
          //     })
          //   })
          // }else{
          //   if(redirect == 'dataset'){
          //     this.$router.push({name: 'AlbumForm', params: {saveToStore: true}})
          //   } else{
          //     this.$router.push({name: 'OrthomosaicForm', params: {saveToStore: true}})
          //   }
          // }
        } else {
          this.$toast.open({
            message: "Formulário inválido, verifique os campos.",
            type: "is-danger",
            position: "is-bottom"
          });
        }
      })
    }
  }
};
</script>

