      width: 8px;
      background-color: #F5F5F5;
    }

    &::-webkit-scrollbar-thumb{
      // border-radius: 10px;
      background-color: #888;
    }

    .content {
      padding: 10px;

      .sectionTitle {
        padding: 3px 0px 3px 0;

        h2 {
          font-size: 20px;
          font-weight: bold;
          line-height: inherit;
        }
      }

      .button{
        margin-top: 20px;
        background-color: $color-light;
      }
